@import url('https://fonts.googleapis.com/css?family=Fredericka+the+Great&display=swap');
@import url('https://fonts.googleapis.com/css?family=Julius+Sans+One|Open+Sans+Condensed:300&display=swap');
@import url('https://fonts.googleapis.com/css?family=Open+Sans+Condensed:300&display=swap');
@media (min-width: 300px) and (max-width: 820px) {
    body {
        margin: 0%;
        padding: 0%;
        background-repeat: no-repeat;
        background-size: cover;
        height: 100%;
        width: 100%;
        position: absolute;
        width: 300px;
        height: 300px;
        text-align: center;
    }
    h2 {
        font-family: 'Fredericka the Great', cursive;
        align-items: center;
        font-size: 400%;
        justify-items: center;
        text-align: center;
        font-weight: 400;
        margin: 0%;
        padding: 0%;
    }
    .photoAbout {
        background-image: url(/static/media/Me.7cc4069a.jpg);
        background-size: cover;
        margin-top: 10%;
        margin-bottom: 10%;
        height: 50vh;
        width: 70%;
    }
    .blockMe {
        justify-content: space-around;
    }
    .txtaboutme {
        font-family: 'Open Sans Condensed', sans-serif;
        width: 70%;
        align-items: center;
        text-align: center;
        justify-content: center;
        display: flex;
        margin-left: 15%;
        margin-right: 15%;
    }
}

@media (min-width: 821px) {
    body {
        margin: 0%;
        padding: 0%;
        background-repeat: no-repeat;
        background-size: 100%;
        height: 100%;
        width: 100%;
        position: relative;
    }
    .blockMe {
        margin-bottom: 10%;
        box-shadow: 5px 5px 15px rgb(0, 0, 0);
        display: flex;
        width: 55%;
        height: 80%;
        margin: 0 auto;
        margin-top: 10%;
    }
    .myPhoto {
        display: flex;
        flex-direction: column;
    }
    .txtAboutMe {
        font-family: 'Open Sans Condensed', sans-serif;
        display: flex;
        flex-direction: column;
        width: 40%;
        font-size: 19px;
        padding: 0px 90px;
        margin: auto;
        line-height: 30px;
    }
    h2 {
        line-height: 65px;
        width: 70%;
        float: center;
        font-family: 'Fredericka the Great', cursive;
        font-size: 50px;
        text-align: center;
        font-weight: 400;
        margin: 10%;
        padding: 0%;
    }
    .blockMe img {
        margin: 10%;
        height: 420px;
        width: 310px;
    }
}